import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
	state: {
		count: 1,
		newsList: [],
		goodsTitleList:[]
	},
	mutations: {
		increase(state, value) {
			state.newsList.push(value)
			console.log(this.state.newsList)	
		},
		delete(state, index){
			console.log('delete'+index)	
			state.newsList.splice(index,1)
		}
	},
	actions: {},
	modules: {}
})
